.introduction .row{height:inherit}.introduction .row .summary{transition:1s}.introduction .row .summary:hover{padding:30px 20px;box-shadow:1px 1px 10px var(--main-color-two);border-radius:10px;transform:scale(1.1, 1.1)}.introduction .row .summary h1.my-name{font-size:40px;color:var(--main-color-two)}.introduction .row .summary h5.job-position{margin-top:25px;margin-bottom:25px;color:var(--main-job-position-color)}.introduction .row .summary p.job-caption{margin:0;line-height:2.5;color:var(--main-color-two);text-align:justify}.introduction .row .summary .contact-me-btn{background-color:#7668bd;color:#FFF}.introduction .row .summary .contact-me-btn:hover{background-color:#7668bd}.introduction .row .contact-links li{font-size:33px;margin-bottom:10px}.introduction .row .contact-links li .icon{color:#7668bd;transition:.5s}.introduction .row .contact-links li .icon:hover{background-color:var(--main-color-two);padding:10px;border-radius:3px}.introduction .row .contact-links li:nth-child(1){animation:fadeInLeft 1s ease-in-out 0s}.introduction .row .contact-links li:nth-child(2){animation:fadeInLeft 1s ease-in-out .5s}.introduction .row .contact-links li:nth-child(3){animation:fadeInLeft 1s ease-in-out 1.5s}.introduction .row .contact-links li:nth-child(4){animation:fadeInLeft 1s ease-in-out 2s}.introduction .row .my-image{width:100%;height:100%;border:3px solid var(--main-color-two);opacity:.6;transition:1s;animation:imageAnimation 2s ease 0s infinite}.introduction .row .my-image:hover{opacity:1;border-radius:50%;padding:10px;border-top-right-radius:15px;border-bottom-left-radius:15px;border-bottom-color:red;border-top-color:green}@keyframes imageAnimation{from{transform:translateY(-10px)}to{transform:translateY(10px)}}@keyframes fadeInLeft{from{opacity:0;transform:translatex(-100px)}to{opacity:1;transform:translatex(0)}}
